@import 'colors';
@import 'fonts';

@import 'sections/principles';

html {
	font-size: 62.5%; }
body {
	@include f-regular;
	color: $black; }
p, h1 {
	margin: 0; }

h1 {
	@include f-bold; }

.border {
	border: 1px solid black;
	&-r {
		border: 1px solid red; } }

.glob-wrap {}

.section {
	position: relative;
	padding: 6rem 3.4rem;
	&__title {
		@include f-bold;
		color: $black;
		font-weight: bold;
		font-size: 6rem;
		line-height: 8rem; } }

.spec-border {
	border: 1rem solid transparent;
	border-image: url('../img/square.svg') 30 round;
	border-image-outset: 0.6rem;
	box-shadow: 0 0 0 2px $blue;
	position: relative;
	&::before {
		content: '';
		display: block;
		height: 2.2rem;
		width: 2.2rem;
		background: url("../img/mouse-icon.svg");
		background-size: cover;
		position: absolute;
		top: -4rem;
		left: 0; } }



